/**
 * Skill Translation Hub Page JavaScript
 *
 * This file contains the JavaScript for the Skill Translation Hub page.
 */

// Import components
import Accordion from '../components/accordion';
import Testimonials from '../components/testimonials';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Skill Translation Hub page initialized');

  // Initialize accordion for FAQ sections
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });

  // Initialize accordion for skill cards
  initializeSkillAccordion();

  // Initialize testimonials
  Testimonials.init({
    testimonialSelector: '.testimonial',
    dotSelector: '.testimonial-dot',
    activeClass: 'active',
    autoRotate: true,
    autoRotateInterval: 5000
  });

  // Set up skill translator
  setupSkillTranslator();

  // Set up skill mapping tabs
  setupSkillMappingTabs();
}

/**
 * Initialize accordion for skill cards
 */
function initializeSkillAccordion() {
  const accordionHeaders = document.querySelectorAll('.accordion-header');

  accordionHeaders.forEach(header => {
    header.addEventListener('click', function() {
      // Toggle active class on the header
      this.classList.toggle('active');

      // Get the content element
      const content = this.nextElementSibling;

      // Toggle the content visibility
      if (content.style.maxHeight) {
        content.style.maxHeight = null;
        // Update the toggle icon
        const toggleIcon = this.querySelector('.accordion-toggle');
        if (toggleIcon) {
          toggleIcon.textContent = '+';
        }
      } else {
        content.style.maxHeight = content.scrollHeight + 'px';
        // Update the toggle icon
        const toggleIcon = this.querySelector('.accordion-toggle');
        if (toggleIcon) {
          toggleIcon.textContent = '-';
        }
      }
    });
  });
}

/**
 * Set up skill translator
 */
function setupSkillTranslator() {
  const translatorForm = document.querySelector('.skill-translator-form');
  const resultContainer = document.querySelector('.skill-translation-result');

  if (translatorForm && resultContainer) {
    translatorForm.addEventListener('submit', function(e) {
      e.preventDefault();

      // Get form data
      const militarySkill = translatorForm.querySelector('select[name="military-skill"]').value;
      const industryField = translatorForm.querySelector('select[name="industry-field"]').value;

      // Show loading state
      resultContainer.innerHTML = '<div class="loading-spinner"></div>';
      resultContainer.style.display = 'block';

      // Simulate API call with timeout
      setTimeout(() => {
        // Generate translation result
        const result = generateTranslationResult(militarySkill, industryField);

        // Display result
        resultContainer.innerHTML = result;
      }, 1000);
    });
  }

  /**
   * Generate translation result based on inputs
   */
  function generateTranslationResult(militarySkill, industryField) {
    // This would normally come from an API or database
    // For now, we'll use a simple mapping

    const skillMappings = {
      'leadership': {
        'tech': 'Team Leadership, Project Management, Strategic Planning',
        'healthcare': 'Clinical Team Leadership, Healthcare Administration, Patient Care Coordination',
        'finance': 'Financial Team Management, Risk Management, Strategic Financial Planning'
      },
      'logistics': {
        'tech': 'Supply Chain Management, IT Asset Management, Resource Allocation',
        'healthcare': 'Medical Supply Chain Management, Healthcare Resource Allocation, Patient Flow Optimization',
        'finance': 'Financial Resource Management, Asset Allocation, Operational Efficiency'
      },
      'communications': {
        'tech': 'Technical Documentation, Cross-functional Collaboration, Client Relations',
        'healthcare': 'Patient Communication, Interdisciplinary Collaboration, Medical Documentation',
        'finance': 'Client Relations, Financial Reporting, Stakeholder Communication'
      }
    };

    if (skillMappings[militarySkill] && skillMappings[militarySkill][industryField]) {
      const skills = skillMappings[militarySkill][industryField].split(', ');

      let html = `
        <h3>Skill Translation Results</h3>
        <p>Your military experience in <strong>${militarySkill}</strong> translates to these valuable skills in the <strong>${industryField}</strong> industry:</p>
        <ul class="translated-skills-list">
      `;

      skills.forEach(skill => {
        html += `<li>${skill}</li>`;
      });

      html += `
        </ul>
        <div class="skill-translation-tips">
          <h4>How to Highlight These Skills</h4>
          <p>Include these skills in your resume and cover letter, and be prepared to discuss specific examples of how you've demonstrated these skills in your military experience during interviews.</p>
        </div>
      `;

      return html;
    } else {
      return `
        <h3>No Translation Available</h3>
        <p>We don't have a specific translation for the combination of ${militarySkill} and ${industryField} yet. Please try another combination or contact us for personalized assistance.</p>
      `;
    }
  }
}

/**
 * Set up skill mapping tabs
 */
function setupSkillMappingTabs() {
  // For industry-specific translations tabs
  const tabButtons = document.querySelectorAll('.tab-button');
  const tabPanels = document.querySelectorAll('.tab-panel');

  if (tabButtons.length > 0) {
    tabButtons.forEach(button => {
      button.addEventListener('click', function() {
        const tabId = this.getAttribute('data-tab');
        const targetPanel = document.getElementById(tabId + '-panel');

        // Remove active class from all buttons and panels
        tabButtons.forEach(btn => btn.classList.remove('active'));
        tabPanels.forEach(panel => panel.classList.remove('active'));

        // Add active class to clicked button and target panel
        this.classList.add('active');
        if (targetPanel) {
          targetPanel.classList.add('active');
        }
      });
    });
  }

  // For skill mapping tabs (if they exist)
  const skillMappingTabs = document.querySelectorAll('.skill-mapping-tab');
  const skillMappingContents = document.querySelectorAll('.skill-mapping-content');

  if (skillMappingTabs.length > 0 && skillMappingContents.length > 0) {
    skillMappingTabs.forEach(tab => {
      tab.addEventListener('click', function() {
        const targetId = this.getAttribute('data-target');
        const targetContent = document.getElementById(targetId);

        // Remove active class from all tabs and contents
        skillMappingTabs.forEach(t => t.classList.remove('active'));
        skillMappingContents.forEach(c => c.classList.remove('active'));

        // Add active class to clicked tab and target content
        this.classList.add('active');
        if (targetContent) {
          targetContent.classList.add('active');
        }
      });
    });

    // Activate first tab by default
    if (skillMappingTabs[0] && skillMappingContents[0]) {
      skillMappingTabs[0].classList.add('active');
      skillMappingContents[0].classList.add('active');
    }
  }
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
